.root {
    position: fixed;
    top: 0;
    left: 0;

    display: flex;
    flex-direction: column;

    background: #f7f7f8;
    width: 100vw;
    height: calc(100vh - 80px);
    box-sizing: border-box;
    overflow-y: scroll;

    -ms-overflow-style: none; /* Internet Explorer 10+ */
    scrollbar-width: none; /* Firefox */
    &::-webkit-scrollbar {
        display: none;
    }
}

.image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: -1;
}